export default {
	data() {
		return {

		}
	},
	methods: {
		//上架
		StartMaterialShelf(e) {
			console.log(e)
			uni.showModal({
				content: e.currentTarget.dataset.testid,
				showCancel: false
			});
			uni.navigateTo({
				url: '../Shelf/Shelf?SplitNo='
			})
		},
		//下架
		StartMaterialDown(e) {
			uni.navigateTo({
				url: '../ShelfDown/ShelfDown'
			})
		},
		//库内移动
		StartStockMove() {
			uni.navigateTo({
				url: '../StockMove/StockMove'
			})
		},
	   //复核
		StartConfirmPickLabel() {
			uni.navigateTo({
				url: '../PickLabelConfirm/PickLabelConfirm'
			})
		},
     	//库存直接下架
		StartStockDelete() {
			uni.navigateTo({
				url: '../StockDelete/StockDelete'
			})
		},
		//拆盘下架
		StartSplitDown(){
			uni.navigateTo({
				url: '../SplitManage/SplitDown'
			})
		},
		StartSplitConfirm(){
			uni.navigateTo({
				url:'../SplitManage/SplitConfirm'
			})
		},
		
		StartSplitShelf(){
			uni.navigateTo({
				url:'../SplitManage/SplitShelf'
			})
		},
	}
}
